import React from 'react'
import {Routes, Route} from 'react-router-dom'
import Home from '../components/Pages/Home/Home'
import Login from '../components/Pages/Login/Login'
import Signup from '../components/Pages/Sighup/Signup'
import MyAccount from '../components/Pages/MyAccount/MyAccount'
import Logout from '../components/Pages/Logout/Logout'
import Container from '../modules/Container'
import TeacherContainer from '../components/Pages/Teacher/TeacherContainer'
import Teacher from '../components/Pages/Teacher/Teacher'
import Demo from '../components/Pages/Teacher/Demo'
import List from '../components/Pages/Teacher/List'
import Myblogs from '../components/Pages/Myblogs/Myblogs'
import AddBlog from '../components/Pages/AddBlog/AddBlog'
import MainContainer from '../modules/MainContainer'
import AdminContainer from '../modules/AdminContainer'
import AdminLogin from '../admincomponents/pages/Login'
import AdminLogout from '../admincomponents/pages/AdminLogout'
import Dashboard from '../admincomponents/pages/Dashboard'
import Blogger from '../admincomponents/pages/Blogger'
import Categories from '../admincomponents/pages/Categories'
import AddCategory from '../admincomponents/pages/AddCategory'
import Image from '../admincomponents/pages/Image'
import ViewBlogByCategory from '../components/Pages/ViewBlogByCategory/ViewBlogByCategory'
import Student from '../admincomponents/pages/Student'
import AdminGroup from '../admincomponents/pages/AdminGroup'
import BlogDetails from '../components/Pages/BlogDetails/BlogDetails'

const AllRoutes = () => {
  return (
    <Routes>
            <Route path='adminlogin' element={<AdminLogin/>} />

        <Route path='' element={<MainContainer/>}>

        <Route path='' element={<Home/>} />
        <Route path='login' element={<Login/>} />
        <Route path='logout' element={<Logout/>} />
        <Route path='view-blog-by-category/:cate' element={<ViewBlogByCategory />} />
        <Route path='blog-details' element={<BlogDetails/>} />

        <Route path='blogger' element={<Container/>}>

          <Route path='my-account' element={<MyAccount/>}/>
          <Route path='my-blogs' element={<Myblogs/>}/>
          <Route path='addblog' element={<AddBlog/>}/>

        </Route>

        <Route path='' element={<TeacherContainer/>}>
          <Route path='teacher' element={<Teacher/>}/>
          <Route path='teacher/list' element={<List/>}/>
          <Route path='teacher/demo' element={<Demo/>}/>
        </Route>

        <Route path='signup' element={<Signup/>} />
        <Route path='my-account' element={<MyAccount/>} />

        </Route>
        <Route path='admin' element={<AdminContainer/>}>

            <Route path='dashboard' element={<Dashboard/>} />
            <Route path='image' element={<Image />} />
            <Route path='logout' element={<AdminLogout/>} />
            <Route path='bloggers' element={<Blogger/>} />
            <Route path='categories' element={<Categories/>} />
            <Route path='categories/add' element={<AddCategory/>} />
            <Route path='categories/update/:a' element={<AddCategory/>} />
            <Route path='student' element={<Student/>} />
            <Route path='admingroup' element={<AdminGroup />} />


        </Route>

    </Routes>
  )
}

export default AllRoutes