import React from 'react';
import ReactDOM from 'react-dom/client';
import {BrowserRouter} from 'react-router-dom'
import {Provider} from 'react-redux'
import {configureStore, combineReducers} from '@reduxjs/toolkit'
import UserAuthSlice from './redux/UserAuthSlice'
import  AdminAuthSlice from './redux/AdminAuthSlice'
import CategorySlice from './redux/CategorySlice';
import StudentSlice from './redux/StudentSlice';
import AdminGroupSlice from './redux/AdminGroupSlice';
import App from './App';

let rootReducer = combineReducers({UserAuthSlice, AdminAuthSlice, CategorySlice, StudentSlice, AdminGroupSlice});

let store = configureStore({reducer : rootReducer});

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <Provider store={store}>
      <BrowserRouter>
       <App />
      </BrowserRouter>
    </Provider>
  </React.StrictMode>
);
