import React, {useEffect} from 'react'
import { useFormik } from 'formik'
import * as YUP from 'yup'
import axios from 'axios'
import { useNavigate, useParams } from 'react-router-dom'
import { API } from '../../Util/API'
import { addNewCate } from '../../redux/CategorySlice'
import {useDispatch} from 'react-redux'

let AddCategorySchema = YUP.object({
    name : YUP.string().required("Add Your Category")
})

const AddCategory = () => {

    let disp = useDispatch();

    let navigate = useNavigate();
    let {handleChange,handleSubmit,errors,touched, values, setFieldValue} = useFormik({
        enableReinitialize : true,
        validationSchema : AddCategorySchema,
        initialValues : {
            name : ""
        },
        onSubmit : (formData)=>{
            if (param.a){
                axios.put(`${API}/category/${param.a}`, formData).then(response=>{
                    // console.log(response.data);
                    navigate("/admin/categories");
                })
            }else{
                axios.post(`${API}/category`, formData).then(response=>{
                    // console.log(response.data);
                    // let obj = { __v : 0, name : formData.name, _id : "000"}
                    disp(addNewCate(response.data.result))
                    navigate("/admin/categories");
                })
            }
        }
    })

    let param = useParams();

    useEffect(()=>{
        if (param.a){
            axios.get(`${API}/category/${param.a}`).then(response=>{
                setFieldValue("name", response.data.name)
            })
        }
    },[])

  return (
    <div className='container my-5'>
        <form onSubmit={handleSubmit}>
        <div className='row'>
            <div className='col-md-8 offset-md-2'>
                <div className='form-group'>
                    <label>{param.a ? 'Update' : 'Add New'} Category Name</label>
                    <input type='text' value={values.name} name='name' onChange={handleChange} className={'form-control '+(errors.name && touched.name ? 'is-invalid' : '')}/>
                    {
                        errors.name && touched.name ? <small className='text-danger'>{errors.name}</small> : ''
                    }
                </div>
                <button type='submit' className='btn btn-primary'>
                    {param.a ? 'Update' : 'Add'}
                </button>
            </div>
        </div>
        </form>
    </div>
  )
}

export default AddCategory