import React, {useState, useRef} from 'react'
import {useSelector, useDispatch} from 'react-redux'
import {useFormik} from 'formik'
import { addStu } from '../../redux/StudentSlice'
import { delStu } from '../../redux/StudentSlice'
import { updateStu } from '../../redux/StudentSlice'

const Student = () => {

  let disp = useDispatch();
  let allStu = useSelector(state=>state.StudentSlice);

  let {handleChange, handleSubmit, values, resetForm, setFieldValue} = useFormik({
    enableReinitialize : true,
    initialValues : {
      _id : "",
      name : "",
      age : ""
    },
    onSubmit : (formData)=>{
      if (newStu._id){
        disp(updateStu(formData));
        setNewStu({_id : "", name : "", age : ""});
      }else{
        disp(addStu(formData))
                 
      }
      resetForm();
    }
  })

  let closeBtn = useRef();
  let [stu, setStu] = useState({});
  let[newStu, setNewStu] = useState({});


  let askDel = (obj)=>{
      setStu(obj)
  }
  let confDel = ()=>{
    disp(delStu(stu));
    closeBtn.current.click()
  }


  let update = (obj)=>{
    // console.log(obj);
    setFieldValue("name", obj.name);
    setFieldValue("age", obj.age);
    setFieldValue("_id", obj._id);
    setNewStu(obj);
  }

  let resetData = ()=>{
    resetForm();
    setNewStu({_id : "", name : "", age : ""});
  }
  return (
    <>
    <div className='container my-5'>

      <form onSubmit={handleSubmit}>
        <div className='row'>
          <div className='col-md-8 offset-md-2'>

            <div className='form-group'>
              <label>Name</label>
              <input type='text' name='name' value={values.name} onChange={handleChange}  className='form-control'/>
            </div>

            <div className='form-group'>
              <label>Age</label>
              <input type='number' name='age' value={values.age} onChange={handleChange} className='form-control'/>
            </div>
           <button type='submit' className='btn btn-success'>{ newStu._id ? 'Update' : 'Add'}</button>
           {newStu._id ? <button onClick={()=>resetData()} className='btn btn-danger'>Cancel</button> : ''}
            
          </div>
        </div>
      </form>

      <div className='row my-4'>
        <div className='col-md-12'>
          <table className="table table-dark">
            <thead>
              <tr>
                <th>S.No.</th>
                <th>Name</th>
                <th>Age</th>
                <th>Delete</th>
                <th>Edit</th>
              </tr>
            </thead>
            <tbody>
              {
                allStu.map((value, index)=>{
                  return(
                    <tr key={index}>
                      <td key={index}>{index + 1}</td>
                      <td>{value.name}</td>
                      <td>{value.age}</td>
                      <td><button onClick={()=>askDel(value)} data-toggle='modal' data-target='#delModal' className='btn btn-sm btn-danger'>Delete</button></td>
                      <td><button onClick={()=>update(value)} className='btn btn-sm btn-info'>Edit</button></td>
                    </tr>
                  )
                })
              }
            </tbody>
          </table>
        </div>
      </div>
    </div>
    <div className='modal fade' id='delModal'>
      <div className='modal-dialog'>
        <div className='modal-content'>
          <div className='modal-header'>
            <h4>Delete Student !</h4>
          </div>
          <div className='modal-body'>
            Are You Sure Want to Delete {stu ? <b>{stu.name}</b> : ''}
          </div>
          <div className='modal-footer'>
            <button onClick={confDel} className='btn btn-danger'>Delete</button>
            <button ref={closeBtn} data-dismiss='modal' className='btn btn-info'>Cancel</button>
          </div>
        </div>
      </div>
    </div>
    </>
  )
}

export default Student