import * as YUP from 'yup'

let AddBlogSchema = YUP.object({
    title : YUP.string().required("Insert Your Title"),
    category : YUP.string().required("Select Your Category"),
    details : YUP.string().required("Add Your Title's Description"),
    image : YUP.mixed().required("select Your Blog Image").test('valid-type', "This File Type is Not Valid", (value)=>{
        // console.log(value);
        
        let arr = value.name.split('.')
        // console.log(arr)
        let ext = arr[arr.length-1];

        if(ext=="jpg" || ext=="png" || ext=="bmp" || ext=="jpeg" || ext=="gif" || ext =="mp4" || ext=="mkv" ||ext=="avi" || ext=="mpeg" || ext=="mp4" || ext=="avi" || ext=="mpeg" || ext =="mkv")
       {
        return true;
       }else{
        return false;
       }

     }).test('valid-size', "Maximum Size Should Not Be More Then 200 MB", (value)=>{
        // console.log(value);
        if(value.size > (1024*1024*200)){
            return false;
        }else{
            return true;
        }
     })
})
export default AddBlogSchema;