import React, {useState, useEffect} from "react";
import {useFormik} from 'formik'
import axios from 'axios'
import {useNavigate} from 'react-router-dom'
import {API} from '../../../Util/API'
import UserSchema from '../../Schema/UserSchema'

let Signup = ()=>{

  let navigate = useNavigate();
  let[city,setCity] = useState([]);
  let[state, setState] = useState([]);

  let {handleChange, handleSubmit, errors, touched} = useFormik({
    validationSchema : UserSchema,
    initialValues : {
      fullname : "",
      username : "",
      password : "",
      repassword : "",
      contact : "",
      city : "",
      state : "",
      address : "",
      gender : ""
    },
    onSubmit : (formData)=>{
      // console.log(formData)
      axios.post(`${API}/user/signup`, formData).then(response=>{
        console.log(response.data);
        navigate("/login")
      })
    }
  })

  useEffect(()=>{
    // axios.get(`${API}/city`).then(response=>{
    //   setCity(response.data)
    // })
       axios.get(`${API}/city/state`).then(response=>{
        // console.log(response.data)
        setState(response.data);
       })
  },[])

  let getCity = (e)=>{
   let x = e.target.value
   axios.get(`${API}/city/state/${x}`).then(response=>{
    //  console.log(response.data)
    setCity(response.data);
   })

  }
    return(
     <>
       <div className="container" style={{minHeight : "650px"}}>
         <form onSubmit={handleSubmit}>
         <div className="row">
          <div className="col-md-8 offset-md-2 my-5">
           <div className="card card-border border-info">
            <div className="card-header bg-info" >
                <h2 className="text-light" style={{fontFamily : "Helvetica"}}>Signup</h2>
            </div>
            <div className="card-body">
                <div className="form-group">
                    <label>Full Name</label>
                    <input type="text" name="fullname" onChange={handleChange} className={'form-control ' + (errors.fullname && touched.fullname ? 'is-invalid' : '')}/>
                    {
                      errors.fullname && touched.fullname ? <small className="text-danger">{errors.fullname}</small> : ''
                    }
                </div>

                <div className="form-group">
                    <label>User Name</label>
                    <input type="email" name="username" onChange={handleChange}  className={'form-control ' + (errors.username && touched.username ? 'is-invalid' : '')}/>
                    {
                      errors.username && touched.username ? <small className="text-danger">{errors.username}</small> : ''
                    }
                </div>

                <div className="form-group">
                    <label>Passwored</label>
                    <input type="password" name="password" onChange={handleChange}  className={'form-control ' + (errors.password && touched.password ? 'is-invalid' : '')}/>
                    {
                      errors.password && touched.password ? <small className="text-danger">{errors.password}</small> : ''
                    }
                </div>

                <div className="form-group">
                    <label>Re-Passwored</label>
                    <input type="password" name="repassword" onChange={handleChange}  className={'form-control ' + (errors.repassword && touched.repassword ? 'is-invalid' : '')}/>
                    {
                      errors.repassword && touched.repassword ? <small className="text-danger">{errors.repassword}</small> : ''
                    }
                </div>

                <div className="form-group">
                    <label>Contact</label>
                    <input type="number" name="contact" onChange={handleChange}  className={'form-control ' + (errors.contact && touched.contact ? 'is-invalid' : '')}/>
                    {
                      errors.contact && touched.contact ? <small className="text-danger">{errors.contact}</small> : ''
                    }
                </div>

                <div className="form-group">
                    <label>Gender</label>
                    <br/>
                    <input type="radio" name="gender" onChange={handleChange} value="male"/>Male  
                    <input type="radio" name="gender" onChange={handleChange} value="female"/>Female
                    <br/>
                    {
                      errors.gender && touched.gender ? <small className="text-danger">{errors.gender}</small> : ''
                    }

                </div>

                <div className="form-group">
                    <label>Address</label>
                    <textarea name="address" onChange={handleChange}  className={'form-control ' + (errors.address && touched.address ? 'is-invalid' : '')}></textarea>
                    {
                      errors.address && touched.address ? <small className="text-danger">{errors.address}</small> : ''
                    }
                </div>

                <div className="form-group">
                    <label>State</label>
                    <select name="state" onChange={(e)=>{handleChange(e); getCity(e)}} className={'form-control ' + (errors.state && touched.state ? 'is-invalid' : '')}>
                      <option>Select Your State</option>
                      {
                        state.map((value,index)=><option key={index}>{value}</option>)
                      }
                    </select>
                    {
                      errors.state && touched.state ? <small className="text-danger">{errors.state}</small> : ''
                    }
                </div>

                <div className="form-group">
                    <label>City</label>
                    <select name="city" onChange={handleChange} className={'form-control ' + (errors.city && touched.city ? 'is-invalid' : '')}>
                      <option>Select Your City</option>
                      {
                        city.map(value=><option key={value._id}>{value.name}</option>)
                      }
                     
                    </select>
                    {
                      errors.city && touched.city ? <small className="text-danger">{errors.city}</small> : ''
                    }
                </div>
            </div>
            <div className="card-footer bg-info">
             <button type="submit" className="btn btn-lg text-info">Signup</button>
            </div>
            </div>
          </div>
         </div>
         </form>
       </div>
     </>
    )
}
export default Signup;