import {createSlice, createAsyncThunk} from '@reduxjs/toolkit'
import axios from 'axios'
import { API } from '../Util/API';

let fatchStu = createAsyncThunk("fatch", async()=>{
    let response = await axios(`${API}/student`);
    return response.data;
})

let fetchOneStu = createAsyncThunk("fatchOne", async (id)=>{
    let response = await axios.get(`${API}/student${id}`);
    return response.data;
})

let addStu = createAsyncThunk("add", async (data)=>{
    let response = await axios.post(`${API}/student`, data);
    return response.data;
})

let delStu = createAsyncThunk("del", async (obj)=>{
    let response = await axios.delete(`${API}/student/${obj._id}`);
    return response.data;
})

let updateStu = createAsyncThunk("update", async (obj)=>{
    let response = await axios.put(`${API}/student/${obj._id}`, obj);
    return response.data;
})

let StudentSlice = createSlice({
    name : "Abc",
    initialState : [],
    extraReducers : {

        [fatchStu.fulfilled] : (state, action)=>{
            return action.payload;
        },
        [addStu.fulfilled] : (state, action)=>{
            state.push(action.payload.result)
        },
        
        [delStu.fulfilled] : (state, action)=>{
            return state.filter(value => value._id != action.payload.result._id);
        },
        [updateStu.fulfilled] : (state, action)=>{
            return state.map(value=>{
                if(value._id == action.payload.result._id){
                    return action.payload.result
                }else{
                    return value
                }
            })
        }
    }
})

export default StudentSlice.reducer;
export {fatchStu, addStu,delStu, updateStu};