import React, { useEffect } from 'react'
import { Outlet } from 'react-router-dom'
import Header from '../admincomponents/shared/Header'
import Footer from '../admincomponents/shared/Footer'
import { useDispatch } from 'react-redux'
import { adminLogin } from '../redux/AdminAuthSlice'
import { fatchStu } from '../redux/StudentSlice'
import { useNavigate } from 'react-router-dom'

const AdminContainer = () => {

  let disp=useDispatch()
  let navigate = useNavigate();


  useEffect(()=>{
    if (localStorage.getItem("lorem")){
      disp(adminLogin())
    }
  }, [])

  useEffect(()=>{
    disp(fatchStu())
  }, [])

  useEffect(()=>{
    if (! localStorage.getItem("lorem")){
      navigate("/");
    }
  })

  return (
          <>
          <Header/>
          <div className='container-fluid' style={{minHeight : "650px"}}>
          <Outlet/>
          </div>
          <Footer/>
          </>
  )
}

export default AdminContainer