import React from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { getAdmin } from '../../redux/AdminGroupSlice';

const AdminGroup = () => {

  let disp = useDispatch();
let adminFetch = useSelector(state => state.AdminGroupSlice);

disp(getAdmin());

  return (
    <>
    <div className='container my-5' style={{minHeight : "650px"}}>
      <div className='row'>
        <div className='col-md-12'>
          <table className="table table-dark">
            <thead>
              <tr>
                <th>S.No</th>
                <th>Name</th>
              </tr>
            </thead>
            <tbody>
              {
                adminFetch.map((value, index)=>{
                  return(
                    <tr>
                      <td>{index+1}</td>
                      <td>{adminFetch.name}</td>
                    </tr>
                  )
                })
              }
            </tbody>
          </table>
            
          
        </div>
      </div>
    </div>
    </>
  )
}

export default AdminGroup;