import React from 'react'
import { NavLink } from 'react-router-dom'
import { URL } from '../../../Util/API'

const Blog = (props) => {
  return (
    <div className="col-lg-3 col-md-6">
        <article className="post-grid mb-5">
            
                {
                  props.data.type=='image' ? <NavLink to={"/blog-details/"}><img src={URL+"/blog-data/"+props.data.image} style={{height :"250px"}} alt="" className="img-fluid w-100"  /> : ''</NavLink>:''
                }
                {
                  props.data.type=="video" ? (
                    <video width="250px" height="250px" controls>
                      <source src={URL+"/blog-data/"+props.data.image} type='video/mp4'/>
                      Your Browser Does Not Support This Video Type
                    </video>
                  ) : ""
                }
            
            <span className="cat-name text-color font-extra text-sm text-uppercase letter-spacing-1">{props.data.title}</span>
            <h3 className="post-title mt-1"><NavLink to="blog-single.html">{props.data.category}</NavLink></h3>
            <span style={{fontWeight : "bold"}} className="text-muted letter-spacing text-uppercase font-sm">
              {
                new Date(props.data.date).getDate()+"-"+(new Date(props.data.date).getMonth()+1)+"-"+new Date(props.data.date).getFullYear()+"-"+new Date(props.data.date).getHours()+"-"+new Date(props.data.date).getMinutes()
              }
            </span>


        </article>
    </div>
  )
}

export default Blog