import react, {useRef} from 'react'
import {useFormik} from 'formik'
import { API } from '../../Util/API'
import axios from 'axios'
import { useNavigate } from 'react-router-dom'


let Image = ()=>{

    let file = useRef();
    let navigate = useNavigate();

    let {handleChange,handleSubmit} = useFormik({
        initialValues : {
            name : '',
            image : ''
        },
        onSubmit : (formData)=>{
            // console.log(formData)
            // console.log(file.current.files[0])
            let myfile = file.current.files[0];

            let frm = new FormData();
            frm.append("name", formData.name);
            frm.append("image", myfile);
            axios.post(`${API}/image`, frm).then(response=>{
                console.log(response.data);
                navigate("/admin/dashboard");
            })
        }
    })
    return(
        <div className='container'>
            <div className='row'>                
                <div className='col-md-8 offset-md-2 my-5'>
                <form onSubmit={handleSubmit}>
                    <div className='from-group'>
                        <label>IMAGE NAME</label>
                        <input type='text' name='name' onChange={handleChange} className='form-control'/>
                    </div>

                    <div className='from-group'>
                        <label>File</label>
                        <input type='file' ref={file} name='image' onChange={handleChange} className='form-control'/>
                    </div>

                    <br/>
                    <div>
                        <button type='submit' className='btn btn-primary'>File Upload</button>
                    </div>
                    </form>
                </div>
                
            </div>
        </div>
    )
}
export default Image;