import React,{useState,useEffect,useRef} from 'react'
import axios from 'axios'
import { API } from '../../Util/API'
import {NavLink, useNavigate} from 'react-router-dom'
import { useDispatch } from 'react-redux'
import { removeCate } from '../../redux/CategorySlice'

const Categories = () => {

  let disp = useDispatch();

  let [allCate, setAllCate] = useState([]);
  let[cate, setCate] = useState({});
  let delBtn = useRef();

  let askDel = (obj)=>{
    // console.log(obj)
    setCate(obj)
  }

  let confDel = ()=>{
    axios.delete(`${API}/category/${cate._id}`).then(response=>{
      console.log(response.data);
      delBtn.current.click();
      setAllCate(()=>allCate.filter(value => value != cate));
      disp(removeCate(cate));
    })
  }
  

  useEffect(()=>{
    axios.get(`${API}/category`).then(response=>{
      setAllCate(response.data);
    })
  },[])

  let navigate = useNavigate();

  let update = (obj)=>{
    navigate(`/admin/categories/update/${obj._id}`)
  }
  return (
    <>
    <div className='container my-5'>
      <div className='row'>
        <div className='col-md-8 offset-md-2'>
          <NavLink to="/admin/categories/add" className='btn btn-primary btn-block my-2'>Add New Category</NavLink>
          <table className="table table-dark">
            <thead>
              <tr>
                <th>S. No.</th>
                <th>Category Name</th>
                <th>Update</th>
                <th>Delete</th>
              </tr>
            </thead>
            <tbody>
              {
                allCate.map((value,index)=>{
                  return(
                    <tr key={value._id}>
                      <td>{index+1}</td>
                      <td>{value.name}</td>
                      <td><button onClick={()=>update(value)} className='btn btn-info btn-sm'>Update</button></td>
                      <td><button onClick={()=>askDel(value)} data-toggle='modal' data-target='#delmodal' className='btn btn-danger btn-sm'>Delete</button></td>                      
                    </tr>
                  )
                })
              }
            </tbody>
          </table>
         
        </div>
      </div>
    </div>
     <div className='modal fade' id='delmodal'>
     <div className='modal-dialog'>
      <div className='modal-content'>
        <div className='modal-header'>
          <h3>Delete Category</h3>
        </div>
        <div className='modal-body'>
          <p>Are You Sure Delete { cate ? <b className='text-danger'>{cate.name}</b> : ''} Category !</p>
        </div>
        <div className='modal-footer'>
          <button onClick={confDel} className='btn btn-danger'>Delete</button>
          <button ref={delBtn} data-dismiss='modal' className='btn btn-success'>Cancle</button>
        </div>
      </div>
     </div>
   </div>
   </>
  )
}

export default Categories