import React,{useState, useEffect} from 'react'
import {useNavigate} from 'react-router-dom'
import axios from 'axios'
import {API} from '../../Util/API'
import { useFormik } from 'formik'
import * as YUP from 'yup'
import {useDispatch} from 'react-redux'
import { adminLogin } from '../../redux/AdminAuthSlice'
import Header from '../shared/Header'
import Footer from '../shared/Footer'


    let AdminSchema = YUP.object({
        name : YUP.string().required("Insert Your User Name"),
        password : YUP.string().required("Insert Your Correct Password")
    })


const AdminLogin = () => {

    let disp = useDispatch();

    let navigate = useNavigate();
    let [errMsg, setErrMsg] = useState("")
    let {handleChange,handleSubmit,errors,touched} = useFormik({
        validationSchema : AdminSchema,
        initialValues : {
            name : "",
            password : ""
        },
        onSubmit : (formData)=>{
            axios.post(`${API}/admin/auth`, formData).then(response=>{
                if (response.data.success == true){
                    localStorage.setItem("lorem", response.data.admintoken);
                    disp(adminLogin());
                    navigate("/admin/dashboard");
                }
                else{
                    if (response.data.errType == 1){
                        setErrMsg("This User Name Was Not Found")
                    }
                    if (response.data.errType == 2){
                        setErrMsg("This Password Is Incorrect")
                    }
                }
            })
        }
    })

    useEffect(()=>{
        if (localStorage.getItem("lorem")){
        navigate("/admin/dashboard")
        }
    },[])

  return (
    <>
    <Header/>
    <div className='container my-5' style={{minHeight : "650px"}}>
        <div className='row'>
            <div className='col-md-8 offset-md-2'>
                {
                    errMsg ? <div className='alert alert-danger'>{errMsg}<button className='close' data-dismiss='alert'>x</button> </div> : ''
                }
                <h2>Admin Login</h2>
                <form onSubmit={handleSubmit}>
                    <div className='form-group'>
                        <label>User Name</label>
                        <input type='text' name='name' onChange={handleChange} className={'form-control '+ (errors.name && touched.name ? 'is-invalid' : '')}/>
                        {
                            errors.name && touched.name ? <small className='text-danger'>{errors.name}</small> : ''
                        }
                    </div>

                    <div className='form-group'>
                        <label>Password</label>
                        <input type='password' name='password' onChange={handleChange} className={'form-control '+ (errors.password && touched.password ? 'is-invalid' : '')}/>
                        {
                            errors.password && touched.password ? <small className='text-danger'>{errors.password}</small> : ''
                        }
                    </div>

                    <button type='submit' className='btn btn-dark btn-block'>Access</button>
                </form>
            </div>
        </div>
    </div>
    <Footer/>
    </>
  )
}

export default AdminLogin