import React, {useState, useEffect, useRef} from 'react'
import { NavLink } from 'react-router-dom'
import axios from 'axios'
import { API } from '../../../Util/API'

const Myblogs = () => {

  let[allBlogs, setAllBlogs] = useState([])
  let[blog, setBlog] = useState({})
  useEffect(()=>{
    axios.get(`${API}/blogs/user`, {
      headers : {'Authorization' : localStorage.getItem("access-token")}
    }).then(response=>{
      // console.log(response.data)
      setAllBlogs(response.data)
    })
  },[])

  let askDelete = (obj)=>{
    setBlog(obj)
  }
  // console.log(allBlogs)

  let confDel = ()=>{
    axios.delete(`${API}/blogs/user/${blog._id}`, {
      headers : {'Authorization' : localStorage.getItem("access-token")}
    }).then(response=>{
      if (response.data.success == true){
        delBtn.current.click();
       setAllBlogs(()=>{
        return allBlogs.filter(value => value != blog)
       })
      }
    })
  }

  let delBtn = useRef();
    
  return (
    <>
    <div className='container my-5'>
        <div className='row'>
            <div className='col-md-12'>
                <h2 className='text-center' style={{fontFamily : "Helvetica"}}><u>Manage Your Blogs</u></h2>
                <div className='container my-5'>
                  <div className='row'>
                    <div className='col-md-10 offset-md-1'>
                <NavLink to='/blogger/addblog' className='btn btn-primary btn-block my-2'>Add New Blog</NavLink>

                      {
                        allBlogs.length > 0 ? 
                      (<>  <table className="table table-dark">
                        <thead>
                          <tr>
                            <th>S.No</th>
                            <th>Title</th>
                            <th>Category</th>
                            <th>Delete</th>
                            <th>Update</th>
                          </tr>
                        </thead>
                        <tbody>
                          {
                            allBlogs.map((c, index)=>{
                              return(
                                <tr key={c._id}>
                                  <td>{index+1}</td>
                                  <td>{c.title}</td>
                                  <td>{c.category}</td>
                                  <td><button className='btn btn-success'>Update</button></td>
                                  <td><button data-toggle="modal" data-target="#delModal" onClick={()=>askDelete(c)} className='btn btn-danger'>Delete</button></td>

                                </tr>
                              )
                            })
                          }
                        </tbody>
                      </table>
                      </> )
                          :
                         (<> <div className='alert alert-danger' data-dismiss='alert'>
                          No Data Found
                          </div></>)
                      }
                      
                    </div>
                  </div>
                </div>
            </div>
        </div>
    </div>
    <div className='modal fade' id='delModal'>
    <div className='modal-dialog'>
      <div className='modal-content'>
        <div className='modal-header'>
          <h3>Delete Blogs !</h3>
        </div>
        <div className='modal-body'>
          <p>Are You Sure Want to Delete { blog ? <b className='text-danger'>{blog.title}</b> : ""}  !</p>
        </div>
        <div className='modal-footer'>
          <button onClick={confDel} className='btn btn-danger'>Delete</button>
          <button ref={delBtn} data-dismiss="modal" className='btn btn-success'>Cancel</button>
        </div>
      </div>
    </div>
  </div>
  </>
  )
}

export default Myblogs