import {useState,useEffect, useRef} from 'react'
import {useNavigate} from 'react-router-dom'
import {useFormik} from 'formik'
import axios from 'axios'
import AddBlogSchema from '../../Schema/AddBlogSchema'
import {API} from '../../../Util/API'

let AddBlog = ()=>{

    let[cate, setCate] = useState([]);
    let navigate = useNavigate();
    let myfile = useRef();

    useEffect(()=>{
        axios.get(`${API}/category`).then(response=>{
            setCate(response.data)
        })
    },[])
    let {handleChange, handleSubmit, errors, touched, setFieldValue} = useFormik({
        validationSchema : AddBlogSchema,
        initialValues : {
            title : "",
            category : "",
            image : null,
            details : ""
        },
        onSubmit : (formData)=>{

            // console.log(myfile.current.files[0])

            let frm = new FormData();
            frm.append("image", myfile.current.files[0]);
            frm.append("formdata", JSON.stringify(formData));
            // console.log(frm)

            axios.post(`${API}/blogs`, frm, {
                headers : {'Authorization' : localStorage.getItem("access-token")}
            }).then(response=>{
                // console.log(response.data)
                navigate("/blogger/my-blogs")
            })
        }
    })
    return(
        <>
        <div className='container my-5'>
            <form onSubmit={handleSubmit}>
        <div className='row'>
            <div className='col-md-8 offset-md-2'>
                <div className='card card-border border-dark'>
                    <div className='card-header'>
                        <h3 style={{fontFamily : "Helvetica"}}>Add Your Blogs</h3>
                    </div>
                    <div className='card-body'>
                        <div className='form-group'>
                            <label>Title</label>
                            <input type='text' name='title' onChange={handleChange} className={'form-control ' + (errors.title && touched.title ? 'is-invalid' : '')}/>
                            {
                                errors.title && touched.title ? <small className='text-danger'>{errors.title}</small> : ''
                            }
                        </div>

                        <div className='form-group'>
                            <label>Category</label>
                            <select name='category' onChange={handleChange} className={'form-control ' + (errors.category && touched.category)}>
                                <option>Select</option>
                                {
                                    cate.map(value=> <option key={value._id}>{value.name}</option>)
                                }                           
                            </select>
                            {
                                errors.category && touched.category ? <small className='text-danger'>{errors.category}</small> : ''
                            }
                        </div>

                        <div className='from-group'>
                            <label>Image</label>
                            <input type='file' ref={myfile} name='image' onChange={(event)=>setFieldValue("image", event.target.files[0])} className={'form-control '+ (errors.image && touched.image)}/>
                            {
                                errors.image && touched.image ? <small className='text-danger'>{errors.image}</small> : ''
                            }
                        </div>

                        <div className='form-group'>
                            <label>Blog Details :</label>
                            <textarea name='details' onChange={handleChange} className={'form-control ' + (errors.details && touched.details)}></textarea>
                            {
                                errors.details && touched.details ? <small className='text-danger'>{errors.details}</small> : ''
                            }
                        </div>
                    </div>
                    <div className='card-footer'>
                        <button type='submit' className='btn btn-dark'>Post</button>
                    </div>
                </div>
            </div>
        </div>
            </form>
    </div>
        </>
    )
}
export default AddBlog