import React,{useState, useEffect} from 'react'
import { useNavigate } from 'react-router-dom'
import { useFormik } from 'formik'
import axios from 'axios'
import { API } from '../../../Util/API'
import UserLoginSchema from '../../Schema/UserLoginSchema'
import {useDispatch} from 'react-redux'
import { doLogin } from '../../../redux/UserAuthSlice'

const Login = () => {

  let disp = useDispatch();

  let navigate = useNavigate();
  let [errMsg, setErrMsg] = useState("")

  let {handleChange, handleSubmit, errors, touched} = useFormik({
    validationSchema : UserLoginSchema,
    initialValues : {
      username : "",
      password : ""
    },
      onSubmit : (formData)=>{
        axios.post(`${API}/user/auth`, formData).then(response =>{
          console.log(response.data)

          if (response.data.success == true){
            let token = response.data.token;
            localStorage.setItem("access-token", token);
            localStorage.setItem("name", response.data.name);
            disp(doLogin());
            navigate("/blogger/my-account")
          }
            else{
              if (response.data.errType == 1){
                setErrMsg(" This username & Password Are Not Found")
              }
              if (response.data.errType == 2){
                setErrMsg("This Password Is Incorrect")
              }
            }
         
        })
      }
    })




  let btncolor = {
    backgroundColor : "#272D33"
  }
  let bodycolor ={
    backgroundColor : "#F8EFEA"
  }

  useEffect(()=>{
    if (localStorage.getItem("access-token")){
      navigate("/blogger/my-account")
    }
  })

  return (
  <div className='container'>
    <form onSubmit={handleSubmit}>
    <div className='row'>
      <div className='col-md-6 offset-md-3 my-5'>
        {
          errMsg ? <div className='alert alert-danger'>
            {errMsg}<button className='close' data-dismiss='alert'>X</button>
          </div> : ''
        }
       <div className='card card-border border-dark' >
        <div className='card-header' style={bodycolor}>
          <b>User Login</b>
        </div>
        <div className='card-body'>
          <div className='form-group'>
            <label>User_Name</label>
            <input type='text' name='username' onChange={handleChange} className={'form-control ' + (errors.username && touched.username ? 'is-invalid' : '')}/>
            {
              errors.username && touched.username ? <small className='text-danger'>{errors.username}</small> : ''
            }
          </div>

          <div className='form-group'>
            <label>Password</label>
            <input type='password' name='password' onChange={handleChange} className={'form-control ' + (errors.password && touched.password ? 'is-invalid' : '')}/>
            {
              errors.password && touched.password ? <small className='text-danger'>{errors.password}</small> : ''
            }
          </div>

        </div>
        <div className='card-footer' style={bodycolor}>
          <button type='submit' style={btncolor} className='btn btn-block'>Log In</button>
        </div>
       </div>
      </div>
    </div>
    </form>
  </div>
  )
}

export default Login;