import {createSlice, createAsyncThunk} from '@reduxjs/toolkit'
import { API } from '../Util/API';
import axios from 'axios';

let getAdmin = createAsyncThunk('get', async ()=>{
    let response = await axios.get(`${API}/admingroup`);
    return response.data;
})

let AdminGroupSlice = createSlice({
    name : "AdminGroupData",
    initialState : [],
    extraReducers : {

        [getAdmin.fulfilled] : (state, action)=>{
            return action.payload;
        }
    }
});

export default AdminGroupSlice.reducer;
export {getAdmin};