import React from "react";
import * as YUP from 'yup'

let UserSchema = YUP.object({
    fullname : YUP.string().required("Insert your Full Name"),
    username : YUP.string().email().required("Insert your Email-id"),
    contact : YUP.number().typeError("contact must be a `number` type").min(1000000000, "Insert Atleast 10 Digit").max(9999999999, "Insert Maximum 10 Digit").required("Insert Your Contact Number"),
    password : YUP.string().matches(/^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{5,}$/,{message :"Must Contain 5 Characters, One Uppercase, One Lowercase, One Number and one special case Character"
}).required("Insert Your Password"),
    repassword : YUP.string().oneOf([YUP.ref('password', "Not Conformed")]).required("Enter Repassword "),
    state : YUP.string().required("Select your State"),
    city : YUP.string().required("Select your city"),
    address : YUP.string().required("Insert your Address"),
    gender : YUP.string().required("select your Gender")
})
export default UserSchema;